<template>
  <section class="calendar">
    <div style="margin-bottom:20px;padding:0;">
      <div class="monthSelect">
        <select v-model="selectMonthObj.year" class="calendarSelect" name="yearSelect">
          <option v-for="item in yearList" :key="item" :value="item" :label="item+''"></option>
        </select>
        <select v-model="selectMonthObj.month" class="calendarSelect" name="monthSelect">
          <template>
            <option v-for="item in monthList" :key="item" :value="item" :label="item+''"></option>
          </template>
        </select>
        <!--<button class="calendarButton" @click="updateCalendarDate('minus')"></button>-->
        <!--<el-date-picker v-model="selectMonth" type="month" placeholder="" @change="handleChangeMonth" format="yyyyMM" value-format="yyyy/M"></el-date-picker>-->
        <!--<button class="calendarButton" @click="updateCalendarDate('add')"></button>-->
      </div>
      <!--<div style="text-align: right;padding:10px;font-size:12px;">-->
        <!--&lt;!&ndash;<img style="vertical-align: middle;" :src="success"><slot name="success"></slot>&ndash;&gt;-->
        <!--&lt;!&ndash;<img style="vertical-align: middle;" :src="waitting"><slot name="waite"></slot>&ndash;&gt;-->
        <!--&lt;!&ndash;<img style="vertical-align: middle;" :src="warning"><slot name="warning"></slot>&ndash;&gt;-->
        <!--&lt;!&ndash;<img style="vertical-align: middle;" :src="cancel"><slot name="cancel"></slot>&ndash;&gt;-->
      <!--</div>-->
    </div>
    <div class="calendarContent">
      <div class="calendarGrid">
        <div v-for="item in weekList" class="calendarGridCellHead">{{item}}</div>
      </div>
      <div v-for="(i,index) in [0,7,14,21,28,35]" class="calendarGrid">
        <div v-for="(item,m) in dateList.slice(i,i+7)" :class="['calendarGridCell',{'noneClickFontColor':item.userSelect === false},{'selectDateFontColor':item.userSelect === true && item.value == selectDate}]" v-bind:style="{ 'height': mainHeight+'px'}" @click="handleClickDate(item.status,item.value,item)">
          <div class="dateNum">
            <span v-if="item.holiday !== undefined" style="font-size: 12px;position: absolute;left: 10px;color:#409EFF;">{{item.holiday}}</span>
            {{item.value}}
          </div>
          <!--<div ref="status" v-show="item.userSelect === true" class="dateStatus">-->
            <!--<img v-show="item.logStatus === '1'" :src="success">-->
            <!--<img v-show="item.logStatus === '0'"  :src="waitting">-->
            <!--<img v-show="item.logStatus === '2'"  :src="warning">-->
            <!--<img v-show="item.status === 'now' && item.logStatus === undefined && item.holidayType !== 0 && (item.holidayType === 1 ||  m !== 0 && m !== 6 ) && item.afterToday === false"  :src="cancel">-->
          <!--</div>-->
        </div>
      </div>
    </div>
  </section>
</template>

<script>
  import * as calendarDateFn from './calendarDate'
  // import success from '@/assets/diary_images/success.png'
  // import waitting from '@/assets/diary_images/waiting.png'
  // import warning from '@/assets/diary_images/warning.png'
  // import cancel from '@/assets/diary_images/cancel.png'
  export default {
    props:{
      thisMonthStatus:{
        type:Array,
        default:function(){
          return []
        }
      }
    },
    computed:{
      selectMonth(){
        return this.selectMonthObj.year+'/'+this.selectMonthObj.month
      }
    },
    data() {
      let yearList = this.rangeArray(1990,new Date().getFullYear()).reverse();
      let monthList = this.rangeArray(1,12,'month');
      let nowYear = new Date().getFullYear()+'';
      let nowMonth = monthList[new Date().getMonth()];
      return {
        // success,
        // waitting,
        // warning,
        // cancel,
        selectDate:new Date().getDate(),
        mainHeight: 200,
        yearList:yearList,
        monthList:monthList,
        selectMonthObj:{
          year:nowYear,
          month:nowMonth
        },
        weekList:['','','','','','',''],
        dateList:new Array(42).fill({value:'--'})
        // dateList1:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}],
        // dateList2:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}],
        // dateList3:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}],
        // dateList4:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}],
        // dateList5:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}],
        // dateList6:[{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'},{value:'--'}]
      };
    },
    methods:{
      rangeArray(start,end,type='year'){
        return Array(end-start+1).fill(0).map((v,i)=>{
          if(type === 'month' && i<9){
            let str = i+start;
            return '0'+str;
          }else{
            return i+start+''
          }
        });
      },
      handleChangeMonth(){
        this.dateList = calendarDateFn.getDateList(this.selectMonth+'/01');
        // let obj = calendarDateFn.getDateList(this.selectMonth);
        // this.dateList1 = obj.dateList1;
        // this.dateList2 = obj.dateList2;
        // this.dateList3 = obj.dateList3;
        // this.dateList4 = obj.dateList4;
        // this.dateList5 = obj.dateList5;
        // this.dateList6 = obj.dateList6;
      },
      updateCalendarDate(opt){
        let returnDate = '';
        let thisMonth = parseInt(this.selectMonth.split('/')[1]);
        let thisYear = parseInt(this.selectMonth.split('/')[0]);
        if(opt === 'add'){
          if(thisMonth === 12){
            thisYear = thisYear + 1;
            thisMonth = 1;
          }else{
            thisMonth = thisMonth+1;
          }
        }else if(opt === 'minus'){
          if(thisMonth === 1){
            thisYear = thisYear - 1;
            thisMonth = 12;
          }else{
            thisMonth = thisMonth - 1;
          }
        }
        thisMonth = thisMonth<10?'0'+thisMonth:thisMonth;
        this.selectMonthObj.year = thisYear+'';
        this.selectMonthObj.month = thisMonth+'';
      },
      handleClickDate(opt,val,item){
        this.selectDate = val;
        let returnValue = this.selectMonth+'/'+val
        switch(opt){
          case 'after':
            this.updateCalendarDate('add');
            break;
          case 'prev':
            this.updateCalendarDate('minus');
            break;
          case 'now':
            this.$emit('clickCalendarFn', returnValue,item);
            break;
        }
      },
      handleLogStatus(statusArray){
        if(statusArray.length !== 0){
          let _dateList = [];
          statusArray.map((item,index)=>{
            let date = item.date.slice(-2);
            _dateList = this.dateList.map((i)=>{
              if(i.status === 'now' && i.value == date) {
                i['logStatus'] = item.status;
                if (item.holiday !== undefined) {
                  i['holiday'] = item.holiday;
                }
                if(item.holidayType !== undefined){
                  i['holidayType'] = item.holidayType
                }
              }
              return i
            })
          });
            this.dateList = _dateList
        }
      },
      callBackUserFn(){
        this.$emit('changeDateFn',calendarDateFn.dateRange)
      }
    },
    mounted(){
      this.handleChangeMonth();
      this.callBackUserFn();
    },
    watch:{
      selectMonth:{
        handler:function(val){
          this.handleChangeMonth();
          this.callBackUserFn();
        },
        deep:true
      },
      thisMonthStatus:{
        handler:function(val){
          this.handleLogStatus(val);
        },
        deep:true
      }
    },
    beforeUpdate(){
      this.mainHeight = $('.calendarGridCell').width();
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .calendar{
    text-align: center;
    color:#606266;
    margin-bottom:20px;
    .monthSelect{
      text-align: left;
      height:40px;
      line-height:40px;
      padding-left:20px;
      .calendarSelect{
        width:100px;
        height:30px;
        border-radius: 5px;
      }
    }
  }
  .calendarContent{
  }
  .calendarGrid{
    display:flex;
    .calendarGridCell{
      flex:1;
      margin:0 10px;
      padding:10px;
      border-top:2px solid #E4E7ED;
      cursor:pointer;
      position:relative;
      .dateNum{
        text-align: right;
      }
      .dateStatus{
        text-align: right;
        position: absolute;
        bottom: 0;
        right: 10px;
      }
    }
    .calendarGridCell:hover{
      background: #e6f7ff;
    }
  }
  .calendarGridCellHead{
    height:50px;
    line-height: 50px;
    flex:1;
  }
  .noneClickFontColor{
    color:#C0C4CC;
  }
  .selectDateFontColor{
    background: #e6f7ff;
    .dateNum{
      color:#409EFF
    }
  }
</style>



