<template>
  <div class="projectContent">
    <calendar @clickCalendarFn = 'showClickDate' @changeDateFn="changeMonthCallBack"></calendar>
  </div>
</template>

<script>
  import calendar from '@/project/calendar/index'
    export default {
        components:{
          calendar
        },
        data() {
          return{}
        },
        methods: {
          showClickDate(date){
            alert(date)
          },
          changeMonthCallBack(dateRange){
            alert(JSON.stringify(dateRange))
          }
        },

    }
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  .projectContent{
    flex:1;
    margin:10px 20px;
    background-color:white;
  }
</style>



