let monthDateNum = [31,28,31,30,31,30,31,31,30,31,30,31];//
let returnAfterFlag = false;

/**
 * 
 * @param selectYear
 * @returns {boolean}
 */
const leapYear = (selectYear) =>{
  let normalLeapFlag = selectYear % 4;
  let centuryYear = selectYear % 100;
  let centuryLeapFlag = selectYear % 400;
  return (centuryYear === 0)?(centuryLeapFlag === 0?true:false):(normalLeapFlag === 0?true:false)
};

/**
 * range
 * @param start
 * @param end
 * @returns {any[]}
 */
const rangeArray = (start, end, userSelect,status) => Array(end - start + 1).fill(0).map((v, i) =>{
  let obj = {};
  if(status === 'now'){
    if(returnAfterFlag === 'thisMonth'){
      let date = new Date().getDate();
      obj.afterToday = date < i+start;
    }else{
      obj.afterToday = returnAfterFlag;
    }
  }
  obj.value = i+start;
  obj.userSelect = userSelect;
  obj.status = status;
  return obj;
});


/**
 * 
 * @param weekDay 
 * @param lastDate 
 */
const  prevShowDateList = (weekDay,lastDate)=>{
  let startDate = lastDate - weekDay + 1;
  return rangeArray(startDate,lastDate,false,'prev');
};

let dateRange = {
  startDate:'',
  endDate:''
};

export {dateRange}


/**
 * 
 * @param selectDate
 */
export function getDateList(selectDate){
  let dateList = selectDate.split('/');
  let selectYear = dateList[0];//
  let selectMonth =  parseInt(dateList[1]);//
  let today = new Date();
  if(selectMonth < today.getMonth()+1 && selectYear <= today.getFullYear()){
    returnAfterFlag = false;
  }else if(selectMonth > today.getMonth()+1 || selectYear > today.getFullYear()){
    returnAfterFlag = true;
  }else if(selectMonth == today.getMonth()+1){
    returnAfterFlag = 'thisMonth';
  }

  let weekDay = new Date(selectDate).getDay();//1

  if(leapYear(selectYear)) {
    monthDateNum[1] = 29
  }

  let _selectMonth = selectMonth-1;//monthDateNum
  let selectDateNum = monthDateNum[_selectMonth];//

  dateRange.startDate = selectYear + '/' + selectMonth +'/'+'1';
  dateRange.endDate = selectYear + '/' + selectMonth +'/'+ selectDateNum;


  let prevDateNum = null;//

  if(_selectMonth === 0){
    prevDateNum = monthDateNum[11];
  }else{
    prevDateNum = monthDateNum[_selectMonth-1]
  }

  //
  let prevList = prevShowDateList(weekDay,prevDateNum);
  //
  let selectList = rangeArray(1,selectDateNum,true,'now');
  //
  let afterNum = 42 - weekDay - selectDateNum;
  let afterList = rangeArray(1,afterNum,false,'after');

  //
  let returnList = prevList.concat(selectList).concat(afterList);
  // let obj = {};
  // let index = 1;
  //
  // for(let i=0;i<returnList.length;i+=7){
  //    obj['dateList'+index] = returnList.slice(i,i+7);
  //    index++;
  // }
  //
  // return obj
  return returnList
}




